@font-face {
  font-family: GoogleSans-Regular;
  src: url("./font/GoogleSans-Regular.ttf");
}

@font-face {
  font-family: GoogleSans-Medium;
  src: url("./font/GoogleSans-Medium.ttf");
}

@font-face {
  font-family: GoogleSans-MediumItalic;
  src: url("./font/GoogleSans-MediumItalic.ttf");
}

body {
  margin: 0;
  padding: 0;
  font-family: "GoogleSans-Regular", Arial, Helvetica, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f3f7f7;
}
